import { GET_ARTICLES,GET_ARTICLES_BY_CATEGORY,GET_FEATURED } from "../types";

let inital_State = {
  articlesList: [],
  categoryArticles: [],
  featured: [],
};
export const HomePageArticles = (state = inital_State, action) => {
  switch (action.type) {
    case GET_ARTICLES:
      return {
        ...state,
        articlesList: action.payload,
      };
    case GET_ARTICLES_BY_CATEGORY:
      return {
        ...state,
        categoryArticles: action.payload,
      };
    case GET_FEATURED:
      return {
        ...state,
        featured: action.payload,
      };

    default:
      return state;
  }
};
