import React, { Component, useEffect, useState } from "react";
import Carousel from "react-spring-3d-carousel";
import { v1 as uuidv4 } from "uuid";
import { config } from "react-spring";
import baseURL from "../../Redux/constantURL";
import { Link } from "react-router-dom";
// import 'carousel.css'
function Carousels({ data }) {
  const [state, setState] = useState({
    goToSlide: 0,
    offsetRadius: 2,
    showNavigation: true,
    config: config.slow,
  });
  const [images, setImages] = useState([]);
  const sliders =
    data &&
    data.length > 0 &&
    data.map((item, index) => {
      var obj = {
        key: uuidv4(),
        content: [
          <Link to={`/description/article/${item.article_id}`}>
            <div>
              <img
                src={baseURL + "/api/uploads/" + item.image}
                alt={item.title}
                title={item.title}
              />
              <p style={{ position: "absolute", color: "white" }}>
                {item.title}
              </p>
            </div>
          </Link>
        ],
      };
      return obj;
    });
  useEffect(() => {
    setImages(sliders);
    // console.log(images);
  }, [data]);
  const slides = [
    {
      key: uuidv4(),
      content: [
        <div>
          <img src="https://picsum.photos/800/801/?random" alt="1" /> ,
          <p style={{ position: "absolute", color: "white" }}>
            Click to read or watch the news
          </p>
        </div>,
      ],
    },
    {
      key: uuidv4(),
      content: (
        <div>
          <img src="https://picsum.photos/800/801/?random" alt="2" /> ,
          <p style={{ position: "absolute", color: "white" }}>
            Click to read or watch the news
          </p>
        </div>
      ),
    },
    {
      key: uuidv4(),
      content: (
        <div>
          <img src="https://picsum.photos/800/801/?random" alt="3" /> ,
          <p style={{ position: "absolute", color: "white" }}>
            Click to read or watch the news
          </p>
        </div>
      ),
    },
    {
      key: uuidv4(),
      content: (
        <div>
          <img src="https://picsum.photos/800/801/?random" alt="4" /> ,
          <p style={{ position: "absolute", color: "white" }}>
            Click to read or watch the news
          </p>
        </div>
      ),
    },
    {
      key: uuidv4(),
      content: (
        <div>
          <img src="https://picsum.photos/800/801/?random" alt="5" /> ,
          <p style={{ position: "absolute", color: "white" }}>
            Click to read or watch the news
          </p>
        </div>
      ),
    },
    {
      key: uuidv4(),
      content: (
        <div>
          <img src="https://picsum.photos/800/801/?random" alt="6" /> ,
          <p style={{ position: "absolute", color: "white" }}>
            Click to read or watch the news
          </p>
        </div>
      ),
    },
    {
      key: uuidv4(),
      content: (
        <div>
          <img src="https://picsum.photos/800/801/?random" alt="7" /> ,
          <p style={{ position: "absolute", color: "white" }}>
            Click to read or watch the news
          </p>
        </div>
      ),
    },
    {
      key: uuidv4(),
      content: (
        <div>
          <img src="https://picsum.photos/800/801/?random" alt="8" /> ,
          <p style={{ position: "absolute", color: "white" }}>
            Click to read or watch the news
          </p>
        </div>
      ),
    },
  ].map((slide, index) => {
    return {
      ...slide,
      onClick: () => setState({ ...state, goToSlide: index }),
    };
  });

  const onChangeInput = (e) => {
    setState({ ...state, [e.target.name]: parseInt(e.target.value, 10) || 0 });
  };

  return (
    <div style={{ width: "80%", height: "500px", margin: "0 auto" }}>
      {images && images.length > 0 && (
        <Carousel
          slides={images}
          title={images.title}
          goToSlide={state.goToSlide}
          offsetRadius={state.offsetRadius}
          showNavigation={state.showNavigation}
          animationConfig={state.config}
        />
      )}
      <div
        style={{
          margin: "0 auto",
          marginTop: "2rem",
          width: "50%",
          display: "flex",
          justifyContent: "space-around",
        }}
      ></div>
    </div>
  );
}

export default Carousels;
