import Axios from "axios";
import { GET_ARTICLES,GET_ARTICLES_BY_CATEGORY,GET_FEATURED } from "../types";
import baseURL from '../constantURL';


export const GET_ARTCLES = () => async (dispatch) => {
   
  const { data } = await Axios.get(baseURL + "/api/categories/get_one/get_last_data")
  console.log(data);
  dispatch({
    type: GET_ARTICLES,
    payload: data,
  });
};

export const GET_ARTCLES_BY_CATEGORY = (body) => async (dispatch) => {
   
  const { data } = await Axios.post(baseURL + "/api/get_articles_by_category",body)
  dispatch({
    type: GET_ARTICLES_BY_CATEGORY,
    payload: data,
  });
};

export const GET_FEATURED_ARTICLES = () => async (dispatch) => {
   
  const { data } = await Axios.get(baseURL + "/api/get_featured_article")
  dispatch({
    type: GET_FEATURED,
    payload: data,
  });
};

