// Assets
import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { GET_ARTCLES_BY_CATEGORY } from "../../Redux/Actions/homePageArticles";
import { useDispatch, useSelector } from "react-redux";
import baseURL from "../../Redux/constantURL"; 
import axios from "axios";
import parser from "html-react-parser";

// Components
function Body(props) {
  const [articleList, setArticlesList] = useState(null);
  const dispatch = useDispatch();
  let _id = props.match.params.id
  let type = props.match.params.type
  console.log(type);
  useEffect(() => {
    if(type == "category"){
      dispatch(GET_ARTCLES_BY_CATEGORY({category_id:_id}));
    }
    if(type == "sub_category"){
      dispatch(GET_ARTCLES_BY_CATEGORY({sub_category:_id}));
    }
  }, [_id]);
  const articles = useSelector((state) => state.articles.categoryArticles);
  useEffect(() => {
    setArticlesList(articles);
  }, [articles]);

  return (
    <main id="main" class="container">
      <section id="content" role="main">
        <div class="itg-layout-container itg-front">
          <div class="itg-layout-container itg-front">
            <div class="row itg-common-section">
              {articleList && articles.length > 0
                ? articles.map((item, key) => {
                    return (
                      <div class="col-md-4 col-sm-4 col-xs-12 mt-50">
                        <div class="widget-help-text">Section Card</div>
                        <div class="itg-widget">
                          <div class="droppable ">
                            <div class="widget-wrapper section_wise_order">
                              <span class="widget-title">
                                <Link
                                  class="pic-no-icon"
                                  to={`/description/article/${item._id}`}
                                >
                                  {item.title}
                                </Link>
                              </span>
                              <div class="data-holder" id="itg-block-7">
                                <div
                                  class="section-ordering video-gif"
                                  id="card_1206533_itg-block-7"
                                >
                                  <div data-tb-region-item>
                                    <Link
                                      class="pic-no-icon"
                                      to={`/description/article/${item._id}`}
                                    >
                                      <img
                                        data-preview-url=""
                                        class="lazyload"
                                        width="370"
                                        height="208"
                                        src={`${baseURL}/api/uploads/${item.image}`}
                                        alt= {item.title}
                                        title= {item.title}
                                      />
                                    </Link>
                                   
                                  </div>
                                  {parser(item.description.slice(0, 250))}
                                  
                                </div>
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>
                    );
                  })
                : null}
            </div>
          </div>
        </div>
      </section>
    </main>
  );
}

export default Body;
